nav .nav-wrapper {
  max-width: 1000px;
  padding: 0 16px;
  margin: 0 auto;
}

section {
  padding: 48px 0;
}

section#footer {
  padding: 24px 0;
}

h1 {
  font-weight: 300;
}

p {
  font-size: 16px;
}

.stat p {
  text-transform: uppercase;
}
.stats {
  display: flex;
  flex-wrap: wrap;
}
.stat {
  border: 1px solid grey;
  border-left: 1px solid grey;
  width: calc(100% * (1/2));
  padding: 32px;
  text-align: center;
}
.counter {
  font-size: 32px;
}

.team {
  display: flex;
  justify-content: center;
}

.blogpost .image-wrapper {
  flex: 4;
  margin: 0px auto;
}

.blogpost .content {
  flex: 4;
}

.blogpost .footer {
  display: flex;
  flex-direction: column;
}

.blogpost .footer .row {
  flex: 2;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.blogpost .footer .material-icons {
  max-width: 24px;
  margin: 0 8px;
}
.blogpost .footer-content {
  display: flex;
}

.blogpost .read-more {
  display: flex;
  justify-content: center;
}


#hero {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

#hero .container {
  height: 90vh;
}

#features .material-icons {
  font-size: 50px;
}


.flex-dir-col {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.jc-center {
  justify-content: center;
}
.jc-space-between {
  justify-content: space-between;
}

.m-r-16 {
  margin-right: 16px;
}

.btn, .btn-large{
  background-color: var(--color-background);
}

.btn:hover, .btn-large:hover{
  background-color: var(--color-transitions);
}

.teal.lighten-1{
  background-color: var(--color-background) !important;
}

.exemplar {
  display: flex;
  justify-content: center-align;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: 0;
}

:root {
  --color-fontH5: #fff;
  --color-fontH5Hover: #97A6A0;
  --color-background: #4E107A;
  --color-transitions: #9F1EFA;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.blogpost {
  margin: 16px;
  display: flex;
  background-color: var(--color-background);
  flex-direction: column;
  align-items: center;
  width: 250px;
  border-radius: 50px;
  /* box-shadow: 1px 1px 10px black; */
}


.image-wrapper,
.responsive-img {
    width: 100%;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}

.content {
  width: 100%;
  color: var(--color-fontH5);
  height: 100px;
}

.content {
    font-family: 'Roboto', sans-serif;
    line-height: 30px;
    margin: 10px;
    text-align: center;
}

.content h5 {
  font-size: 17px;
  font-weight: 500;
}

.content h5:hover {
  font-size: 20.4px;
  transition: ease-in-out;
  transition-duration: 0.4s;
  color: var(--color-fontH5Hover)
}